import React, { Component } from 'react';
import '../assets/css/news.css';

class NewsList extends Component {
	constructor(props) {
    super(props)
  }

  render() {
    const list = []

    for(let i = 0; i < 6; i++) {
      list.push(
        <li className="news__item" key={i}>
          <article className="news__article">
            <a className="news__article-link" href="/news">
              <div className="news__article-thumb" style={{  backgroundImage: `url(${require("../assets/images/sample.png")})`}}>
              </div>

              <time className="news__article-date">2020.06.19</time>

              <h3 className="news__article-title">
                サンプルテキストサンプル ルテキストサンプルテキストサンプルテキストサンプル ルテキスト
              </h3>
            </a>
          </article>
        </li>
      )
    }

    return (
      <div className="news">
        <h2 className="news__title">NEWS</h2>

        <ul className="news__list">
          {list}
        </ul>

        <a className="btn-more" href="#">
          <span>Load More</span>
        </a>
      </div>
    )
  }
}

export default NewsList;
