import React, { Component } from 'react';
import '../assets/css/slider.css';

class Slider extends Component {
	constructor(props) {
		super(props)

    this.state = {
      slides: [
        {
          slide: require('../assets/images/slider/slider1.jpg'),
					text: '<span>サンプルテキスト</span><span>サンプル ルテキスト</span><span>サンプルテキスト</span>'
        },
        {
          slide: require('../assets/images/slider/slider2.jpg'),
					text: '<span>サンプルテキスト</span><span>サンプル ルテキスト</span><span>サンプルテキスト</span>'
        },
        {
          slide: require('../assets/images/slider/slider3.jpg'),
					text: '<span>サンプルテキスト</span><span>サンプル ルテキスト</span><span>サンプルテキスト</span>'
        }
      ],
      activeSlide: 0,
			delay: 4,
      translateValue: 0,
    }

    this.prevSlide = this.prevSlide.bind(this)
    this.nextSlide = this.nextSlide.bind(this);
	}

	componentDidMount() {
		this.timerId = setInterval(() => {
      this.nextSlide();
    },this.state.delay * 1000);
	}

	componentWillUnmount() {
		clearInterval( this.timerId );
	}

  prevSlide() {
    let slide = this.state.activeSlide - 1 < 0
		? this.state.slides.lenght - 1
		: this.state.activeSlide - 1;

    this.setState({
      activeSlide: slide
    })
  }

  nextSlide() {
    let slide = this.state.activeSlide + 1 < this.state.slides.length
		? this.state.activeSlide + 1
		: 0;

    this.setState({
      activeSlide: slide
    })
  }

  goToSlide = (index) => {
    this.setState({
      activeSlide: index
    })
  }


	render () {
    const { slides, activeSlide } = this.state;
    const controls = []


    for(let i = 0; i < slides.length; i++) {
      controls.push(<span className={`slider__dot-item ${ i === activeSlide? 'is-active' : ''}`} key={i} onClick={() => {
        this.goToSlide(i)
      }}></span>)
    }

		return (
			<div className="slider">
        <ul className="slider__list">
          {slides.map((slide, index) => (
            <li className={`slider__item ${index === activeSlide? 'is-active' : ''}`} key={index}>
              <div className="slider__img" style={{  backgroundImage: `url(${slide.slide})`}}>
              </div>
							<div className="slider__txt" dangerouslySetInnerHTML={{ __html:slide.text }} />
            </li>
          ))}
        </ul>

        <div className="slider__prev" onClick={this.prevSlide.bind(this)}>
          <i className="ico-arrow-slider"></i>
        </div>
        <div className="slider__next" onClick={this.nextSlide.bind(this)}>
          <i className="ico-arrow-slider ico-arrow-slider--next"></i>
        </div>

        <div className="slider__dot">
           {controls}
        </div>
      </div>
		)
	}
}

export default Slider;
